.navbar{
    background:rgb(16, 6, 6);
    height: 5rem;
    width:100vw;
    max-width:100%;
    margin:0 auto;
    padding:1rem-2rem;
    display:flex;
    align-items:center;
    justify-content:space-between;
    position:sticky;
    top:0;
    z-index:3;
}
.logo{
    object-fit:cover;
    height:5rem;
    width:6srem;

}
.desktopMenuListItem{
    margin:2rem;
    cursor:pointer

}
.desktopMenuListItem:hover{
    color:yellow;
    padding-bottom:0.5rem;
    border-bottom:3px solid yellow;
}
.desktopMenuBtn{
    background: white;
    color:black;
    border:none;
    display:flex;
    align-items: center;
    justify-content: center;
    padding:0 1rem;
    border-radius:2rem;
}
.desktopMenuImg{
    object-fit:cover;
    height:1rem;
    width:1rem;
    margin:0.4rem;

}
