#skills{
    overflow:hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin:0 auto;
}
.skillTitle{
    font-size:3rem;
    font-weight:600;
    padding-top:3rem;
    margin-bottom: 1.5px;
}
.skillDesc{
    font-weight:300;
    font-size:1rem;
    max-width:51rem;
    padding:0.2rem;
}
.skillBars{
    margin:1.5rem;
    width:100vw;
    max-width:65rem;
    text-align:left;

}
.skillBar{
    
    display:flex;
    margin:flex;
    padding:1rem 2rem;
    border-radius:1rem;
    background:rgb(50,50,50)
}
.skillBarImg{
    object-fit:cover;
    height:3rem;
    width:3rem;
    margin-right:2rem;
}
.skillBarText>p{
    font-size:0.9rem;
    font-weight:200;
}